import { NextResponse } from 'next/server';
import { cookies } from 'next/headers';
import { logger } from '@/lib/logger';
import { getAdminDb } from '@/lib/firebase-admin';
import { validateCSRF } from '@/lib/csrf';
import { rateLimit, getClientIP } from '@/lib/rate-limit';

const FIREBASE_API_KEY = process.env.NEXT_PUBLIC_FIREBASE_API_KEY!;
const SESSION_COOKIE = '__session';
const SESSION_MAX_AGE = 3600; // 1 hour (matches Firebase token expiry)

/**
 * POST /api/auth/session
 * Receives a Firebase ID token, verifies it via Firebase REST API,
 * and sets an httpOnly session cookie for middleware to check.
 *
 * Uses Admin SDK to check admins collection (bypasses Firestore rules).
 */
export async function POST(request: Request) {
  try {
    // SEC-010: CSRF validation
    const csrf = await validateCSRF(request);
    if (!csrf.valid) {
      return NextResponse.json({ error: 'Invalid request' }, { status: 403 });
    }

    // SEC-012: Server-side rate limiting (10 attempts per 5 minutes per IP)
    const ip = getClientIP(request);
    const rl = await rateLimit(`auth-session:${ip}`, { limit: 10, windowSeconds: 300 });
    if (!rl.allowed) {
      logger.warn('Auth session rate limit exceeded', { ip });
      return NextResponse.json(
        { error: 'Too many login attempts. Please try again later.' },
        { status: 429 }
      );
    }

    const { idToken } = await request.json();

    if (!idToken || typeof idToken !== 'string') {
      return NextResponse.json({ error: 'Missing idToken' }, { status: 400 });
    }

    // Verify token via Firebase Identity Toolkit REST API
    const verifyRes = await fetch(
      `https://identitytoolkit.googleapis.com/v1/accounts:lookup?key=${FIREBASE_API_KEY}`,
      {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ idToken }),
      }
    );

    if (!verifyRes.ok) {
      return NextResponse.json({ error: 'Invalid token' }, { status: 401 });
    }

    const data = await verifyRes.json();
    if (!data.users || data.users.length === 0) {
      return NextResponse.json({ error: 'User not found' }, { status: 401 });
    }

    // Extract user UID from Firebase response
    const uid = data.users[0].localId;
    if (!uid) {
      return NextResponse.json({ error: 'Invalid user data' }, { status: 401 });
    }

    // Verify user is admin by checking admins collection via Admin SDK
    try {
      const db = getAdminDb();
      const adminDoc = await db.collection('admins').doc(uid).get();

      if (!adminDoc.exists) {
        logger.warn('Non-admin login attempt', { uid });
        return NextResponse.json({ error: 'Unauthorized' }, { status: 403 });
      }
    } catch (error) {
      logger.error('Error checking admin status', { uid }, error);
      return NextResponse.json({ error: 'Internal server error' }, { status: 500 });
    }

    // Set httpOnly session cookie
    const cookieStore = await cookies();
    cookieStore.set(SESSION_COOKIE, idToken, {
      httpOnly: true,
      secure: process.env.NODE_ENV === 'production',
      sameSite: 'lax',
      maxAge: SESSION_MAX_AGE,
      path: '/',
    });

    return NextResponse.json({ success: true });
  } catch (error) {
    logger.error('Session API error', { route: '/api/auth/session' }, error);
    return NextResponse.json({ error: 'Internal server error' }, { status: 500 });
  }
}

/**
 * DELETE /api/auth/session
 * Clears the session cookie on logout.
 */
export async function DELETE() {
  const cookieStore = await cookies();
  cookieStore.set(SESSION_COOKIE, '', {
    httpOnly: true,
    secure: process.env.NODE_ENV === 'production',
    sameSite: 'lax',
    maxAge: 0,
    path: '/',
  });

  return NextResponse.json({ success: true });
}
